import React, { useContext, useEffect } from 'react';
import './../../Css/landing-page-style.css';
import { CustomerContext } from '../Contexts/CustomerContext';
import axios from 'axios';
import { ContextReset } from '../Contexts/ContextReset';
import { useForm } from 'react-hook-form';

const CustomerRegistration = () => {
   const [ custRegister, setcustRegister ] = useContext(CustomerContext);
   const [ myreset, setReset ] = useContext(ContextReset);
   const { register, handleSubmit, errors } = useForm();

//    useEffect(()=>{
//       setcustRegister([{firstName:'', lastName:'', email:'', phoneNumber:''}]);
//    },[]);

    const onSubmit = (data) =>{
         console.log(data);
         setcustRegister(data);
         setTimeout(()=>console.log(custRegister),2000);
         //console.log(custRegister);
    }

    const Form_clear = (e) => {
        e.preventDefault();
        setcustRegister([{firstName:'', lastName:'', email:'', phoneNumber:''}]);
        console.log(custRegister);
        const x = document.getElementsByClassName("input-form");
        for(var i = 0; i < x.length; i++){
            x[i].value = "";
        } 
    }

    
        return(
            <div id="reg-form" className="register-form">
                {/* <h1>Create your account</h1> */}
                <form onSubmit={ handleSubmit(onSubmit) }>
                   <input className="input-form cust-registration" type="text" name="firstName"  ref={register} placeholder="Enter your firt name"/><br/>
                   <input className="input-form cust-registration" type="text" name="lastName"   ref={register} placeholder="Enter your last name" /><br/>
                   <input className="input-form cust-registration" type="text" name="email"      ref={register} placeholder="Enter your email" /><br/>
                   <input className="input-form cust-registration" type="text" name="phoneNumber" ref={register} placeholder="Enter your phone number" /><br/>

                   <input className="input-form cust-registration" type="text" name="CustAddress1"   ref={register} placeholder="Your address" /><br/>
                   <input className="input-form cust-registration" type="text" name="custAddress2"   ref={register} placeholder="Apartment, house, office #" /><br/>
                   <input className="input-form cust-registration" type="text" name="custCity" ref={register} placeholder="Your city" /><br/>
                   <div style={{justifyContent:"center", display:"flex"}}>
                   <ul style={{ display:"grid", gridTemplateColumns:"50% 50%", listStyle:"none", width: "100%", marginTop: "5px" }}>
                       <li>
                        <select className="input-form2" name="custState" id="cust_sate"  >
                                <option value="minnesota">Minnesota</option>
                                <option value="ohio">Ohio</option>
                                <option defaultValue value="wisconsin">Wisconsin</option>
                                <option value="Texas">Texas</option>
                        </select><br/>
                       </li>
                       <li>
                        <input className="input-form2" type="text" name="zipCode"   ref={register} placeholder="Your zip code" /><br/>
                       </li>
                   </ul>
                   </div>

                   <div style={{justifyContent:"center", display:"flex"}}>
                    <ul style={{ display:"grid", gridTemplateColumns:"50% 50%", listStyle:"none", width: "100%", marginTop: "5px" }}>
                        <li>
                            <button className="form-btn-cust-register"  type="submit">Register</button> <br/>  
                        </li>
                        <li>
                            <button className="form-btn-cust-register" type="button" onClick={ (e) => Form_clear(e) }>Clear</button> <br/>
                        </li>
                    </ul>
                   </div>

                   
                      
                </form>

                
            </div>
        )
}

export default CustomerRegistration;